body {
  background: #f1f1f1;
  color: #5a5a5a;
  font-size: 1.1rem;
}

.navbar {
  background: #fff;
  box-shadow: 1px 5px 11px -3px #dadada;
}

.nav-tab {
  margin-right: 0;
  margin-left: 0;
  border: 1px solid #dee2e6;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
}

.navbar .navbar-brand {
  font-weight: 700;
  margin-left: 5vw;
  color: #818181;
  transition: all 0.15s;
}

.navbar .navbar-brand:hover,
.navbar .navbar-brand:visited {
  color: #7e7e7e;
}

.navbar-nav .nav-item .active {
  color: #4a89dc !important;
  border-bottom: 3px solid #4a89dc;
  font-weight: 600;
  border-radius: 5%;
}

@media only screen and (max-width: 768px){
  .navbar-nav .nav-item .active {
    color: #4a89dc !important;
    border-left: 2px solid #4a89dc;
    border-bottom: 0;
    border-radius: 0;
    padding-left: 5px;
  }
}

.nav-fill .nav-link {
  font-size: 1.2rem;
  color: #575757;
  background-color: #fcfcfc;
}

.nav-fill .active {
  font-weight: bold;
  color: rgb(10, 126, 221) !important;
  background-color: #fff !important;
  border-bottom: 3px solid #4a77f3;
}

.card .card-header {
  padding: 14px 25px;
  background-color: #fff;
  font-weight: 500;
  color: rgb(88, 88, 88);
  font-size: 1.15rem;
  text-align: center;
  text-transform: uppercase;
}

.list-files {
  margin-top: 3px;
}

.item-close {
  cursor: pointer;
  transition: all 0.15s;
}

.item-close:hover {
  transform: scale(1.1);
  color: tomato;
}

.dropzone {
  border: 2px dashed #dedede;
  border-radius: 5px;
  background: #f5f5f5;
}

.dropzone i {
  font-size: 5rem;
}

.dropzone .dz-message {
  color: rgba(0, 0, 0, 0.54);
  font-weight: 500;
  font-size: initial;
  text-transform: uppercase;
}

.link {
  text-decoration: none;
}

textarea[readonly] {
  background-color: transparent;
  border: 0;
  box-shadow: none;
}

.bd-clipboard {
  position: relative;
  float: right;
  top: 30px;
  right: -6px;
}
.bd-clipboard + .highlight {
  margin-top: 0;
}
@media (min-width: 768px) {
  .bd-clipboard {
    display: block;
  }
}
.btn-clipboard {
  position: absolute;
  top: 0.65rem;
  right: 0.65rem;
  z-index: 10;
  display: block;
  padding: 0.25rem 0.5rem;
  font-size: 0.65em;
  color: #0d6efd;
  background-color: #fff;
  border: 1px solid;
  border-radius: 0.25rem;
}
.btn-clipboard:hover,
.btn-clipboard:focus {
  color: #fff;
  background-color: #0d6efd;
}

.form-control[disabled],
.form-control[readonly],
fieldset[disabled] .form-control {
  border: 1px solid #ced4da;
  background-color: #f8f8f8;
}

.notfound-container {
  height: 93.5vh;
  text-align: center;
}

.notfound-404 .d-block span {
  font-size: 15rem;
  color: #262626;
}

.notfound-404 p {
  font-size: 2rem;
  font-weight: 400;
}

@media only screen and (max-width: 767px) {
  .notfound-404 .d-block span {
    font-size: 10rem;
    color: #262626;
  }
  
  .notfound-404 p {
    font-size: 1.5rem;
    font-weight: 400;
  }
}
@media only screen and (max-width: 480px) {
  .notfound-404 .d-block span {
    font-size: 8rem;
    color: #262626;
  }
  
  .notfound-404 p {
    font-size: 1rem;
    font-weight: 400;
  }
}
